body {
	-webkit-text-size-adjust: none;
	margin: 0;
	font: 14px/1.5 'Noto Sans KR', 'SCoreDream', sans-serif;
}
h1,h2,h3,h4,h5,h6,p,figure,ul,ol,dl,dd,fieldset,pre {margin: 0;}
ol, ul, td, fieldset {padding: 0;}
input, select, textarea, button, pre, a {
	font : {
		family: inherit;
		size: inherit;
		weight: inherit;
	}
	line-height: inherit;
	letter-spacing: inherit;
	color: inherit;
	text-decoration: none;
}
input, select, textarea {
	margin: 0; padding: 0 10px;
	-webkit-appearance: none;
  border: 1px solid;
	border-radius: 0;
	box-sizing: border-box;
	vertical-align: top;
}
input[type="checkbox"],
input[type="radio"] {
  opacity: 0;
}
input[type="password"],
input[type="text"] {
  height: 34px;
  padding: 0 10px;
  border: 1px solid #cdd8db;
}
select::-ms-expand {display:none;}
select {
  padding: 0 10px;
  background: #fff url('../images/icon/icon_select.png') no-repeat right 10px top 50%;
}
li {list-style: none;}
table {
	width: 100%;
  table-layout: fixed;
	border-collapse: collapse;
	border: 0;
}
fieldset, img {border: 0;}
img {
	vertical-align: top;
	max-width: 100%;
}
label, td img {vertical-align: middle;}
h1,h2,h3,h4,h5,strong,b,th {font-weight: 600;}
legend, caption, .blind {display: none;}
address, em {font-style: normal;}
button {
	margin: 0;
	padding: 0;
	overflow: visible;
	cursor: pointer;
	background: none;
	border: 0;
	border-radius: 0;
	> span {position: relative;}
}
pre {white-space: pre-line;}



