.paging {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 36px 0 0;
  .btn_nav {
    position: relative;
    width: 14px; height: 30px;
    font-size: 0;
    margin: 0 4px;
    overflow: hidden;
    &:before, &:after {
      content: "";
      position: absolute;
      top: 50%; left: 50%;
      margin: -5px 0 0 -5px;
      width: 10px; height: 10px;
      border: solid #005fae;
      border-width: 2px 2px 0 0;
      box-sizing: border-box;
    }
    &_start, &_prev {
      &:before, &:after {
        transform: rotate(-135deg);
      }
    }
    &_start:before {margin-left: 0;}
    &_next, &_end {
      &:before, &:after {
        transform: rotate(45deg);
      }
    }
    &_end:before {margin-left: -10px;}
    &_prev:before, &_next:before {display: none;}
    &:disabled {
      opacity: .5;
      cursor: default;
    }
  }
}
.paging_number {
  display: flex;
  margin: 0 5px;
  button {
    min-width: 30px;
    height: 30px;
    box-sizing: border-box;
    border: 1px solid #ececec;
    border-radius: 5px;
    &:disabled {
      color: #005fae;
      cursor: default;
    }
  }
  button ~ button {margin-left: 5px;}
}

