.input_chk {
  position: relative;
  input[type="checkbox"] {
    position: absolute;
    ~ span:before {
      content: "";
      display: inline-block;
      width: 19px; height: 19px;
      margin-right: 8px;
      background: #fff url('../images/icon/icon_chk2.png') no-repeat 50% 50%;
      border: 1px solid #c1c1c1;
      box-sizing: border-box;
      vertical-align: -3px;
    }
    &:checked ~ span:before {
      background-color: #5f6ee2;
      border-color: #5f6ee2;
    }
  }
  input[type="radio"] {
    position: absolute;
    ~ span:before {
      content: "";
      display: inline-block;
      width: 21px; height: 21px;
      margin-right: 7px;
      background: #fff url('../images/icon/icon_radio.png') no-repeat;
      border: 1px solid #c1c1c1;
      border-radius: 50%;
      box-sizing: border-box;
      vertical-align: -3px;
    }
    &:checked ~ span {
      color: #2f37d3;
    }
    &:checked ~ span:before {
      background-position: -1px 100%;
      border: 2px solid #5663e0;
    }
  }
}

