.cal_nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 82px;
  padding: 0 40px;
  .cal_month {
    display: flex;
    align-items: center;
  }
  .month {
    font: {
      weight: 500;
      size: 25px;
    }
    color: #5f63d3;
  }
  .today {
    width: 91px; line-height: 30px;
    text-align: center;
    background: #ececec;
    border-radius: 30px;
    margin-left: 7px;
    font-size: 16px;
    color: #868698;
  }
  .btn_nav {
    width: 34px; height: 34px;
    border: 1px solid #e0e9eb;
    background-image: linear-gradient(to top, #f6f8f9, #fff);
    font-size: 0;
    &:after {
      content: "";
      display: block;
      width: 100%; height: 100%;
      background: url('../images/btn/btn_cal_month.png') no-repeat;
    }
  }
  .btn_nav_next:after {
    transform: rotate(180deg);
  }
}
.cal_body {
  width: 960px;
  margin: 0 auto;
  border: {
    width: 1px 0 0 1px;
    style: solid;
    color: #f1f2f2;
  }
  user-select: none;
  .item {
    position: relative;
    border: {
      width: 0 1px 1px 0;
      style: solid;
      color: #f1f2f2;
    }
    box-sizing: border-box;
  }
  &_day {
    display: flex;
    flex-wrap: wrap;
  }
  &_day .item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 14.2%;
    height: 45px;
    font-size: 15px;
    color: rgba(127, 127, 127, 0.55);
  }
  &_day .sun {
    width: 14.8%;
    color: #ff486f;
  }
  &_date {
    display: flex;
    flex-wrap: wrap;
  }
  &_date .item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 14.2%; min-height: 122px;
    padding: 30px 6px 0;
  }
  &_date .sun {
    width: 14.8%;
    .date {color: #ff486f;}
  }
  &_date .date {
    position: absolute;
    top: 4px; left: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px; height: 30px;
    font-size: 15px;
    border-radius: 10px;
    color: #646464;
  }
  &_date .today .date {
    color: #fff;
    background: #5f63d3;
    font-weight: bold;
  }
  .reg_items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%; height: 28px;
    padding: 0 4px 0 8px;
    border-radius: 4px;
    box-sizing: border-box;
    &.isDragging {
      position: fixed;
      cursor: grabbing;
      z-index: 10;
    }
    select {
      width: 51px; height: 22px;
      color: #3a3a3a;
      background: #fff;
      border-color: #e6ebec;
      padding: 0 7px;
      line-height: 1.2;
    }
    &:nth-of-type(1) {
      background: #edeaff;
      color: #6856cc;
    }
    &:nth-of-type(2) {
      background: #ffe3fa;
      color: #c33370;
    }
    &:nth-of-type(3) {
      background: #c9f3f3;
      color: #1c9b9b;
    }
  }
  .reg_items ~ .reg_items { margin-top: 2px; }
  .btn_reg {
    width: 95px;
    height: 34px;
    margin-top: -20px;
    border-radius: 6px;
    font-size: 16px;
    background-color: #f5f0f8;
    color: #9255e6;
    opacity: .7;
    &:disabled {
      opacity: 1;
      cursor: default;
    }
  }
}

.detail_box {
  display: none;
  flex-direction: column;
  position: fixed;
  top: 0; left: -100vh;
  z-index: 100;
  background: #fff;
  width: 355px; height: 361px;
  border: solid 2px #ececec;
  box-sizing: border-box;
  &.isShow {display: flex;}
  &_tit {
    height: 64px;
    padding: 15px 22px;
    box-sizing: border-box;
    font: {
      size: 21px;
    }
    border-bottom: 2px solid #ececec;
  }
  &_input {
    flex: 1;
    padding: 0 20px;
    .row {
      display: flex;
      align-items: center;
      margin-top: 11px;
    }
    .tit {
      width: 80px;
      font-size: 17px;
      color: #4a4a4c;
    }
    .cont {
      flex: 1;
      input, select {
        width: 100%; height: 39px;
        font-size: 17px;
        color: #4a4a4c;
        border: 1px solid #cfcfcf;
      }
      .cal {
        background: url('../images/icon/icon_cal.png') no-repeat right 10px top 50%;
      }
    }
  }
  .btn_wrap {
    padding: 20px 0;
    background: #f8f8f8;
  }
  .btn_type {
    width: 121px; height: 40px;
    border-radius: 20px;
    font-size: 16px;
  }
}
